.list {
  list-style: none;
}

.link {
  text-decoration: none;
  font-size: 20px;
  color: #212121;
}
.item {
  max-width: 500px;
  border: 1px solid grey;
  border-radius: 10px;
  margin-bottom: 8px;
  padding: 4px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 50px 100px -20px,
    rgba(0, 0, 0, 0.3) 0px 30px 60px -30px,
    rgba(10, 37, 64, 0.35) 0px -2px 6px 0px inset;
}
.item:hover {
  background-color: yellow;
}
.item:nth-child() {
  margin-bottom: 8px;
}
